.titul{
    text-decoration-line: underline;
    font-family: 'Source Serif Pro', serif;
    margin: 0;
    padding-bottom: 2rem;
    font-weight: 700;
    font-size: 38px;
    text-align: left;
    color: #FF4341;
}

.cont{
    justify-content: space-around;
    padding-top: 5rem;
    background-color:rgba(255,255,255,.8);
    box-shadow:0 0 .25em rgba(0,0,0,.25);
    border-radius:.25em;
    width: 1010px;
    height: 370px;
    margin: auto;
    padding: 25px;
}

.cont_jobs{
    display: flex;
}

.jobs{
    display: grid;
    background-color:rgba(255,255,255,.8);
    box-shadow:0 0 .25em rgba(0,0,0,.25);
    border-radius:.25em;
    width: 300px;
    height: 200px;
    margin: 10px;
    padding: 25px;
}

.jobs-tit{
    font-family: 'Source Serif Pro', serif;
    width: 250px;
    height: 70px;
    font-weight: bold;
    font-style: normal;
    font-size: 18px;
    text-align: center;
    color: #D59D6C;
    text-shadow: 0px 0.1px 0.1px #3C3839;    
}

.jobs-an{
    font-family: 'Source Serif Pro', serif;
    font-weight: bold;
    font-style: normal;
    font-size: 18px;
    text-align: center;
    color: #3C3839;
    text-shadow: 0px 0.1px 0.1px #D59D6C;
}

.jobs-btn{
    width: 70px;
    height: 35px;
    font-size: 15px;
    font-family: 'League Spartan', sans-serif;
    text-align: center;
    margin: auto;
    background-color: #FF4341;
    text-shadow:0 0 .25em rgba(0,0,0,.25);
    text-decoration: none;
    border-radius:.25em;
    box-shadow:0 0 .25em rgba(0,0,0,.25);
}

.tex-btn{
    text-decoration: none;
    text-align: center;
    color: #E5E5E5;
}