h1{
    font-family: 'Source Serif Pro', serif;
    text-align: center;
    font-size: 48px;
    line-height: 56px;
}

h3{
    font-family: 'Source Serif Pro', serif;
    text-align: center;
    font-size: 18px;
    line-height: 21px;
}

p{
    font-family: 'Source Sans Pro', sans-serif;
    text-align: center;
    text-align: justify;
}

.cont_1{
    background-color: rgba(213, 157, 108, 0.9);
    border-radius:.25em;
    box-shadow:0 0 .25em rgba(0,0,0,.25);
    box-sizing:border-box;
    left:50%;
    padding:10vmin;
    position:fixed;
    text-align:center;
    top:50%;
    transform:translate(-50%, -50%);
}

.logo{
    left: 120px;
    top: 96px;
}

.tit_1{
    font-family: 'Source Serif Pro', serif;
    text-align: center;
    color: #E5E5E5;
    text-shadow: 0px 4px 4px #FF4341;
    padding: 30px 0 30px;
}

