.conte{
    width: 600px;
    height: 420px;
}

.top{
    padding: 5px;
}

.list{
    padding-left: 25px;
    padding-right: 75px;
}

ul{
    list-style: none;
    padding: 10px;
}

li {
    position: relative;
    padding-left: 20px;
    margin-bottom: 10px
}

li:before {
    position: absolute;
    top: 0;
    left: 0;
    font-family: FontAwesome;
    content: "\f058";
    color: #FF4341;
    padding-right: 20px;
}