*{
    margin: 0;
    border:0;
    padding: 0;
    box-sizing: border-box;
}

.barr{
    background-color: #FF4341;
    padding: 2px;
}

.navb{
    background-color: #a19081;
    padding: 1rem;
    display: flex;
    justify-content: space-around;
}

.item{
    font-family: 'Source Sans Pro', sans-serif;
    color: #E5E5E5;
    text-decoration: none;
    text-shadow: 0px 1px 1px #3C3839;
}

.item:hover{
    background-color: #D59D6C;
    border-radius:.25em;
    padding: 0 1rem;
    text-shadow: 0px 1px 1px #FF4341;
}

/* -------------------------------------------------FONDO------------------------------------- */

.bg {
    animation:slide 3s ease-in-out infinite alternate;
    background-image: linear-gradient(-60deg, #E5E5E5 50%, #FF4341 50%);
    bottom:0;
    left:-50%;
    opacity:.5;
    position:fixed;
    right:-50%;
    top:0;
    z-index:-1;
}

.bg2 {
    animation-direction:alternate-reverse;
    animation-duration:4s;
}

.bg3 {
    animation-duration:5s;
}

@keyframes slide {
    0% {
        transform:translateX(-25%);
    }
    100% {
        transform:translateX(25%);
    }
}